:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	cursor: pointer;
	display: inline-block;
}

.pressed::before {
	border-bottom: 2px solid black;
	border-right: 2px solid black;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	content: '';
	display: inline-block;
	height: 1em;
	margin: -1em 0.5em 0 0;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	width: 0.5em;
}

.popup { }

.addon { }

.disabled,
.disabled:hover {
	cursor: default;
}



