:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	composes: root from './grid-row.m.css';
	line-height: 30px;
	border-left: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.loading {
	margin: 6px;
	width: 12px;
	height: 12px;
	border: 3px solid #bbb;
	border: 3px solid var(--disabled-color);
	border-radius: 50%;
	border-top-color: #424242;
	border-top-color: var(--component-color);
	-webkit-animation: spin 1s ease-in-out infinite;
	        animation: spin 1s ease-in-out infinite;
	will-change: auto;
}

@-webkit-keyframes spin {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spin {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}



