:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	border-top-style: none;
	border-right-style: none;
	padding: 2px 4px;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	min-width: 100px;
}

.input {
	background: #fff;
	background: var(--component-background);
	color: #424242;
	color: var(--component-color);
}

.edit {
	composes: visuallyHidden focusable from '../common/styles/base.m.css';
}



