:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {}

.cell {
	border: 1px solid #424242;
	border: 1px solid var(--component-color);
	border-top-style: none;
	border-right-style: none;
	border-bottom-style: none;
	font-weight: 600;
	padding: 6px 4px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	min-width: 100px;
}

.sortable {
	cursor: pointer;
}

.sorted {
	color: green;
	color: var(--success-color);
}

.desc {}

.asc {}

.sort {
	margin-left: 4px;
	opacity: 0;
}

.sort:focus,
.sorted .sort {
	opacity: 1;
}

.filter {
	margin-top: 4px;
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	color: #424242;
	color: var(--component-color);
	padding: 2px 4px;
}



